/* default */
.default_color {
  background: -webkit-linear-gradient(20deg, #DE733A, #EA8D47);
  background: linear-gradient(70deg, #DE733A, #EA8D47);
  color: #FFFFFF;
}

.default_color:active {
  background: #FFFFFF;
  border-style: solid;
  border-color: #260e04;
  border-width: 0.5px;
}

/* contrast */
.contrast_color {
  background: -webkit-linear-gradient(290deg, teal, #17a2b8);
  background: linear-gradient(160deg, teal, #17a2b8);
  color: #FFFFFF;
}

.contrast_color:active {
  background: #FFFFFF;
  color: #007bff;
  border-style: solid;
  border-color: #260e04;
  border-width: 0.5px;
}

/* darker */
.darker_color {
  background: -webkit-linear-gradient(20deg, #FF5D00, #DE733A);
  background: linear-gradient(70deg, #FF5D00, #DE733A);
  color: #FFFFFF;
}

.darker_color:active {
  background: #FFFFFF;
  color: #DE733A;
  border-style: solid;
  border-width: 0.5px;
}

/* lighter */
.lighter_color {
  background: -webkit-linear-gradient(20deg, #EA8D47, #ffbe75);
  background: linear-gradient(70deg, #EA8D47, #ffbe75);
  border-right-color: #edb191;
  color: #FFFFFF;
}

.lighter_color:active {
  background: #FFFFFF;
  color: #DE733A;
  border-style: solid;
  border-color: #260e04;
  border-width: 0.5px;
}

/* negative */
.negative_color {
  background-color: #FFFFFF;
  color: #DE733A;
  border-style: solid;
  border-color: #976f50;
  border-width: 1px;
}

.negative_color:active {
  background: #DE733A;
  color: #FFFFFF;
  border-style: solid;
  border-color: #260e04;
  border-width: 0.5px;
}

.leaning_right_color {
  background: -webkit-linear-gradient(20deg, #DE733A, #EA8D47);
  background: linear-gradient(70deg, #DE733A, #EA8D47);
  color: #FFFFFF;
}

.leaning_left_color {
  background: -webkit-linear-gradient(20deg, #EA8D47, #DE733A);
  background: linear-gradient(70deg, #EA8D47, #DE733A);
  color: #FFFFFF;
}

.plain_color {
  background: -webkit-gradient(linear, left bottom, left top, from(#EA8D47), to(#DE733A));
  background: -webkit-linear-gradient(bottom, #EA8D47, #DE733A);
  background: linear-gradient(0deg, #EA8D47, #DE733A);
  color: #FFFFFF;
}

